@import url('https://fonts.googleapis.com/css?family=Handlee');
body{
  font-family: 'Handlee', cursive;
}
.App {
  text-align: center;
}


header{
  display: -ms-flexbox;
  display: flex;
  background-color: #273c44;
  -ms-flex-pack: justify;
      justify-content: space-between;
  padding-bottom: 10px;
}
.header-left > img{
  width: 150px;
  margin-top: 20px;
  margin-left: 10px;
}
.header-right > div.btn-hamburguer{
  display:none;
  cursor: pointer;
  margin-top: 30px;
  margin-right: 20px;
  
}
div.btn-hamburguer div{
  width: 30px;
  height: 3px;
  background-color: white;
  margin: 5px 0;
}
.menu-list{
  list-style-type: none;
  margin: 15 0 0 0;
  padding: 0;
  overflow: hidden;
}

.menu-list > li{
  display: inline;
  cursor: pointer;
}
.menu-list > li > a {
  display: inline-block;
  color: white;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}
.mobile-menu{
 background-color: #273c44;
 width: 100%;
 position: absolute;
 display: none;
}
.mobile-menu > ul{
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

.mobile-menu > ul > li{
  padding: 8px 0;
  text-align: left;  
  cursor: pointer;
  
}
.mobile-menu > ul > li:hover{
  background-color: black;
  
}
.mobile-menu > ul > li > a {
  color:white;
  padding: 14px 16px;
  text-decoration: none;
}
.show-mobile-menu{
  display: block;
}


.intro{
 background-color: #273c44;
 text-align: center;
 color: white; 
 height: 100%;
 padding-bottom: 30px;
 padding-top: 100px;
 width: 100%;
}
.intro > div{
  max-width: 450px;
  margin: 0 auto;
  width: 100%;
 }
.intro> div > input.search-models{
  border-radius: 5px 0 0 5px;
  border-color: transparent;
  max-width: 350px;
  width: 100%;
  height: 30px;
  font-size:12px;
}
.intro > div > input.search-models-button{
  height: 36px;  
  border-radius: 0 5px 5px 0;
  border-color: transparent;
  font-size:12px;
  background-color: yellowgreen;
  color:white;
  font-weight: 600;
}


.brands{
  height: 80px;
  width: 100%;
  background-color: aliceblue;
}
.cars-list{
  width: 100%;
  max-width: 600px;
  height: 100%;
  margin: 0 auto;
}
.cars-list div{
  text-align: left;
  
}
.cars-list div > div.list{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.car-info{
  width: 170px;
  font-size: 10px;
  margin: 0 5px;
}
.car-info > img {
  max-width: 170px;
}

.how-it-works{
  margin-top: 50px;
  padding-top:10px;
  color: white;
  background: #43B86A; /* For browsers that do not support gradients */
  background: -webkit-linear-gradient(left,#004A46, #43B86A); /*Safari 5.1-6*/
  background: -o-linear-gradient(right,#004A46, #43B86A); /*Opera 11.1-12*/ /*Fx 3.6-15*/
  background: -webkit-gradient(linear, left top, right top, from(#004A46), to(#43B86A));
  background: -webkit-linear-gradient(left, #004A46, #43B86A);
  background: -o-linear-gradient(left, #004A46, #43B86A);
  background: linear-gradient(to right, #004A46, #43B86A); /*Standard*/

}
.how-it-works div.container > div.image {
  width: 300px;
  margin: 0 20px;  
  }
.how-it-works div.container > div.image > img{
  width:100%;
  max-width: 300px;
  }
.how-it-works > div.container{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  
  -ms-flex-pack: distribute;
  
      justify-content: space-around;
  margin: 0 auto;
  width: 100%;
  max-width: 800px;
  
}
.how-it-works > h2.heading{
  text-align: center;

}
.how-it-works > div.container > div.details{
  text-align: justify;
  max-width: 250px;
}
.onde-estamos{
  text-align: left;
}
.onde-estamos > h1{
  text-align: center;
}
.onde-estamos > div.container{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  margin: 0 auto;
  width: 100%;
  max-width: 800px;
}
.onde-estamos > div.container div{
  width: 200px;
  margin: 0 20px;

}
.onde-estamos > div.container div > p{
  font-size: 12px;
}
footer{
  height: 80px;
  width: 100%;
  background-color: aliceblue;
}

ul.brands-autocomplete{
  list-style-type: none;
  padding: 0;
  overflow: hidden;
  background-color: white;
  color:black;
  border: 1px black solid;
  width: 100%;
  margin: 0 auto;
}
ul.brands-autocomplete li{
  padding: 2px 0;
  text-align: left;

}
ul.brands-autocomplete li:hover{
  background-color: beige;

}

@media screen and (max-width: 370px){
  .cars-list div > div.list{
    -ms-flex-pack: center;
        justify-content: center;
    text-align: left;
  }  
}
@media screen and (max-width: 530px){
  .cars-list div h4{
    text-align: center;
    
  }
  .cars-list div > div.list{
    text-align: left;
  }
}
@media screen and (max-width: 465px){
  .intro > div{
    width: 350px;
  }
  .intro > div > input.search-models-button{
    border-radius: 5px;
    display: none;
  }
  
  .intro > div > input.search-models{
    border-radius: 5px ;
  }  
}
@media screen and (max-width: 380px){
  .intro > div{
    width: 280px;
  }  
  
}
@media screen and (max-width: 453px){
  .header-right > div.btn-hamburguer{
    display:block;
  } 
  .menu-list{
    display: none;
  }
}
@media screen and (min-width: 454px){
  .mobile-menu{
    display: none !important;
  }
}



